import React, {useState } from 'react';
import axios from 'axios';

import CircularProgress from '@mui/material/CircularProgress';
import { useParams } from 'react-router-dom';
import Divider from '@mui/material/Divider';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import { styled } from '@mui/material/styles';
import { Avatar, Box, Button, InputBase} from '@mui/material';

import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft';
import GameRoom from '../Room/GameRoom';

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

 // Total number of images
const i=Math.floor(Math.random() * 18) + 1
export default function Room() {

  const totalImages = 18;

  const [currentImageIndex, setCurrentImageIndex] = useState(i);

  const [loading, setLoading] = useState(false)
  
  const [uid,setUid] = useState(null)
  const [joined,setJoinded]= useState(false)

  const handleCreateRoom = () => {
    if (!loading) {
      setLoading(true);
    }
  };

  const handleImageChange = (newImageIndex) => {
    setFormData({
      ...formData,
      face: newImageIndex,
    });
  };

  const handlePrevImage = () => {
    const i = (currentImageIndex === 1 ? totalImages : currentImageIndex - 1)
    setCurrentImageIndex(i);
    handleImageChange(i);
  };


  const handleNextImage = () => {
    const i = (currentImageIndex === totalImages ? 1 : currentImageIndex + 1)
    setCurrentImageIndex(i);
    handleImageChange(i);
  };

 
  const { id } = useParams();
  const [formData, setFormData] = useState({
        name: '',
        face:currentImageIndex,
        url:id
  });
    
  const handleChange = (e) => {
        //setUData({ ...formData, [e.target.name]: e.target.value });
        setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
        e.preventDefault();
        console.log(e.target.name)
        try {
            handleCreateRoom()
            const response = await axios.post('http://localhost:3001/jpr', formData);
            if(response.data.server==="Ok"){
              setUid(response.data.uid)
              setJoinded(true)
            }
            else{
              setLoading(false);
              alert(response.data)
        
            }

        } catch (error) {
          console.error(error);
          
        }
      };

  return (
    (joined===false?
    <div>
      <Stack
        divider={<Divider orientation="vertical" flexItem />}
        direction="row"
        justifyContent="center"
        alignItems="center"
        spacing={2}
        sx={{height:window.innerHeight}}
      >
       
        <Item>
        <Box
        sx={{
        width: 330,
        height: 260,
        padding:2,
        backgroundColor: 'rgba(8, 219, 132, 0.9);',
         }}
         >
           
            <Stack justifyContent="center" spacing={2}>
            
              
             <Paper component="form" sx={{ p: '2px 6px', display: 'flex', alignItems: 'center'}}>
                <InputBase name="name" onChange={(e)=>handleChange(e)} sx={{ ml: 1, flex: 1 }} placeholder="Enter your Name" inputProps={{ 'aria-label': 'search google maps' }} />
             </Paper>
            
              <Stack direction={"row"} justifyContent="center" alignItems="center">
                <KeyboardArrowLeftIcon sx={{color:"white",fontSize: 35,cursor:"pointer"}} onClick={() => handlePrevImage()}/>
                <Avatar
               alt="Meme Face"
               src={"Faces/jpeg/"+currentImageIndex.toString()+".jpg"}
               sx={{ width: 175, height: 150}}
               variant="rounded"
               name="face"
               value={currentImageIndex} 
                />
                <KeyboardArrowRightIcon sx={{color:"white",fontSize: 35,cursor:"pointer"}} onClick={() => handleNextImage()}/>
              </Stack>
             
         
          <Stack>
             <Button onClick={(e)=>handleSubmit(e)} name="create" type="submit" disabled={loading} variant="contained" sx={{color:"black",backgroundColor:"white",'&:hover': {backgroundColor: 'white',}}}>
              Play!
             </Button>   
             {loading && (
          <CircularProgress
            size={24}
            sx={{
              color: "white",
              position: 'relative',
              left: '47%',
              marginTop:"-30px",
            }}
          />)}
            </Stack>


            </Stack>
            
         </Box>
        </Item>
        
      </Stack>
    </div>
    :(<><GameRoom userid={uid} udata={formData}></GameRoom></>))
  );
}
