import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import io from 'socket.io-client';


import Friends from './Friends/Friends';
import { InputBase } from '@mui/material';

export default function GameRoom (props) {
  const [socket, setSocket] = useState(null);
  const [playerlist, setPlayerList] = useState([]);
  const [host, setHost] = useState(false);
  const [h,seth] = useState(false);

  const [time, settime] = useState(null);


  const [img, setimg] = useState(null)
  const [cap, setCap] = useState(null);

  const [playercaptions,setPlayercaptions] = useState(null);

  const [scores, setScores]=useState({});

  const [gamestart, setGameStart] = useState(false)
  const [gamestate, setGamestate] = useState("notstarted")

  const [curep,setCurep] = useState(null);
  const [isset,setIsset] = useState(false)
  const [maxep, setMaxep] = useState(8);
  const [maxtime, setMaxtime] = useState(30);

  const { id } = useParams();
  const roomId = id || props.id;

  
  useEffect(() => {//For Host
    if (socket) {
      socket.emit('gamestate', {room:roomId});
    }
  }, [socket]);
  
  useEffect(() => {//For Host
    if (props.id) {
      setHost(true);
    }
  }, [props.id]);

  useEffect(() => {
    // Connect to the Socket.io server
    const newSocket = io('ws://localhost:3002/');
    

    // Handle Socket.io events
    newSocket.on('connect', () => {
      console.log(newSocket.id)
      console.log('Connected to Socket.io server');
      newSocket.emit('join', {room:roomId,user:props.userid,uinfo:props.udata,ishost:host});
    });

    newSocket.on('gamestepcaptioning', (data) => {
      if (data!==null){
        setGameStart(true);
        setimg(data.img);
        setGamestate("showingimg")
      }
    });

    newSocket.on('gamesteprating', (data) => {
      if (data!==null){
        setGameStart(true);
        setPlayercaptions(data.data)
        setGamestate("ratingimg")
      }
    });

    newSocket.on('endgamestep', (data) => {
      settime(null);
      setScores(data.data);
      setCurep(data.ep);
      setGamestate("endgamestep");
    });

    newSocket.on('endgame', (data) => {
    settime(null);
    const leaderboardEntries = Object.entries(data.data);
    leaderboardEntries.sort((a, b) => b[1].score - a[1].score);
    setScores(leaderboardEntries);
    setCurep(data.ep);
    setGamestate("endgame");  
    });

    newSocket.on('gamestate', (data) => {
      if (data.state!=="notstarted"){
        setGameStart(true);
        setMaxtime(data.maxtime);
        setMaxep(data.maxep);
        setCurep(data.curep);
        setIsset(true);
        setimg(data.curimg)
      }
    });

    newSocket.on('time', (data) => {
        settime(data);
    });

    newSocket.on('start', (data) => {
      setGameStart(data.start);
      setMaxtime(data.maxtime);
      setMaxep(data.maxep);
      setCurep(data.curep);
      setIsset(true)
    });

    newSocket.on('playerlistcheck', (data) => {
      console.log(data);
      if (data[props.userid]){
        seth(data[props.userid].ishost);
      }
      setPlayerList(data);
    });

    setSocket(newSocket);

    return () => {
      
      newSocket.disconnect();
    };
  }, [id, props,roomId,host]);

  const start = (maxep,maxtime,room) => {
    if (socket) {
      setIsset(true);
      socket.emit('start', {maxep:maxep,room:room,maxtime:maxtime});
    }
  };

  const sendcaption = (u,r,c) => {
    if (socket) {
      socket.emit('caption', {user:u,room:r,caption:c});
    }
  };
  

  const sendscore = (p,r) => {
    if (socket) {
      socket.emit('sendscore', {p:p,u:props.userid,r:r});
    }
  };

  const setscore = (key,score) => {
    setPlayerList((prevplayerlist) => ({
      ...prevplayerlist,
      [key]: {
        ...prevplayerlist[key],
        "score": Number(score)
      }
    }))
  };
  

  return (
    <>
    {gamestate!=="notstarted"?curep.toString()+"/"+maxep.toString():""}


    {time!==null?"Time:"+time.toString():""}

    <br/>


    {img!==null && gamestate==="showingimg"? 
    <>
    <video src={img} loop={true} width="480" height="270" autoPlay={true} />
    <InputBase name="caption" onChange={(e)=>setCap(e.target.value)} sx={{ ml: 1, flex: 1, border:"1px solid black" }} placeholder="Enter the Caption!" inputProps={{ 'aria-label': 'search google maps' }} />
    <button onClick={()=>sendcaption(props.userid,roomId,cap)}>Send</button>
    </>
    :
    ""
    }
     
     {scores!==null && gamestate==="endgame"?
    <>
    EndGame<br/>
     {scores.map(([key, value], index) => (
            <tr key={key}>
              <td>{index + 1}</td>
              <td>{value.uinfo.name}</td>
              <td>{value.score}</td>
            </tr>
          ))}
    </>
    :""}
 

    {scores!==null && gamestate==="endgamestep"?
    <>
     {Object.entries(scores).map(([key, item]) => (
          <React.Fragment key={key}>
            {item.uinfo.name} {item.score} <br/>
          </React.Fragment>
         ))}
    </>
    :""}


   {playercaptions!==null && gamestate==="ratingimg"? 
    <>
    <video src={img} loop={true} width="480" height="270" autoPlay={true} />
    <br/>
    {playerlist? (
          <>
          {Object.entries(playerlist).map(([key, item]) => (
          <React.Fragment key={key}>
          {key !== 'gamestate' ? (
          <>  
          {key!==props.userid?
           <>
           { playercaptions[key] && playercaptions[key].cap !== null?
           <>
           {playercaptions[key].cap}
           <InputBase name="score" onChange={(e)=>setscore(key,e.target.value)} sx={{ ml: 1, flex: 1, border:"1px solid black" }} placeholder="Enter the Caption!" inputProps={{ 'aria-label': 'search google maps' }} />
           <br/>
           </>
           :""}
           </>
           :""}
          
          </>
          ) : null}
          </React.Fragment>
         ))}
         <button onClick={()=>sendscore(playerlist,roomId)}>Send Score</button>
       </>
      ) : (
        <p>No data available.</p>
      )}
    </>
    :
    ""
}
    
    <br/>
    {props.userid}
    {h!==true?<>
      {gamestart===true?"":"Waiting for Host"}
    </>:""}
    
    <br/>
    {h===true?" http://localhost:3000/"+roomId:""}
     <Friends playerlist={playerlist}/>
     
     {h===true && isset===false?
     <>
      <InputBase onChange={(e)=>setMaxep(e.target.value)} sx={{ ml: 1, flex: 1, border:"1px solid black" }} placeholder="Max Ep" inputProps={{ 'aria-label': 'search google maps' }} />
      <InputBase onChange={(e)=>setMaxtime(e.target.value)} sx={{ ml: 1, flex: 1, border:"1px solid black"  }} placeholder="Ep Time" inputProps={{ 'aria-label': 'search google maps' }} />
      <button onClick={()=>start(maxep,maxtime,roomId)}>Start</button>
      </>
      :""}
    </>
  );
}
