import { Avatar, Paper, Stack, styled } from '@mui/material'
import React, { useEffect, useState } from 'react'

const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  }));

  
export default function Friends(props) {
    const [playerlist, setPlaylist] = useState({})
    
    useEffect(() => {
        
        setPlaylist(props.playerlist)
      return () => {
        
      }
    }, [props])
    
  return (
    <Stack spacing={2} width={200}>
        
        {playerlist? (
          <>
          {Object.entries(playerlist).map(([key, item]) => (
  <React.Fragment key={key}>
    {key !== 'gamestate' ? (
      <Item>
        <Avatar
          alt="name"
          src={`Faces/jpeg/${item.uinfo.face}.jpg`}
          sx={{ width: 50, height: 50 }}
          variant="round"
          name="face"
        />
        {item.uinfo.name}
      </Item>
    ) : null}
  </React.Fragment>
))}
       </>
      ) : (
        <p>No data available.</p>
      )}
      </Stack>
  )
}
